import wPicker from "@/components/w-picker/w-picker.vue";
export default{
	components: {
		wPicker
	},
	data(){
		return{
			gender:['a学校', 'b学校', 'c学校', 'd学校', 'e学校', 'f学校'],
			genClass:['一年级', '二年级', '三年级', '四年级', '五年级', '六年级'],
			grade:['1班', '2班', '3班', '4班', '5班', '6班'],
			uploadInfo:{
				school:'',		//学校
				class:'',		//年级
				classClass:'',	//班级
				province: '', 	//省份
				city: '', 		//城市
				area: '', 		//地区
				region: [],
			}
		}
	},
	methods:{
		//学校点击
		bindSchoolChange(e){
			this.uploadInfo.school=e.target.value;
		},
		//年级点击
		bindClassChange(e){
			this.uploadInfo.class=e.target.value;
		},
		//班级点击
		bindGradeChange(e){
			this.uploadInfo.classClass=e.target.value;
		},
		//地区点击出现选项
		toggleTab() {
			this.$refs.region.show();
		},
		//地区选择点击
		bindCityChange(e) {
			this.uploadInfo.region=e.checkArr;
		},
		//发送验证码点击
		getcode(){
			
		},
		//确定点击
		save(){
			
		}
	}
}
